.container-button {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
}

.text-address {
    font-size: calc(6px + 2vmin);
    margin: 20px auto 40px auto;
    color: yellow;
    /* box-shadow: -1px 1.2em 1em black; */
}

.agree {
    background: rgb(44, 201, 22);
    /* background: linear-gradient(90deg, rgba(44, 201, 22, 1) 10%, rgba(65, 180, 98, 1) 24%, rgba(53, 192, 90, 1) 37%, rgba(13, 197, 215, 1) 60%, rgba(44, 114, 187, 1) 80%, rgba(106, 44, 187, 1) 91%); */
    opacity: 1;
    width: 250px;
    height: 50px;
    font-size: calc(6px + 1.5vmin);
    transition-duration: 0.4s;
    border-radius: 20px;
    margin: 5px;
    padding: 5px;
}

.no {
    background: rgb(201, 22, 22);
    /* background: linear-gradient(90deg, rgba(201, 22, 22, 1) 10%, rgba(180, 140, 65, 1) 24%, rgba(192, 53, 121, 1) 37%, rgba(215, 13, 68, 1) 60%, rgba(159, 44, 187, 1) 80%, rgba(187, 44, 44, 1) 91%); */
    opacity: 1;
    width: 250px;
    height: 50px;
    font-size: calc(6px + 1.5vmin);
    transition-duration: 0.4s;
    border-radius: 20px;
    margin: 5px;
    padding: 5px;
}

.agree:hover {
    background-color: white;
    /* border-color: green;
    border-width:  10px; */
    box-shadow: 0 12px 16px 0 rgba(24, 175, 24, 1), 0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

.no:hover {
    background-color: white;
    /* border-color: red;
    border-width:  10px; */
    box-shadow: 0 12px 16px 0 rgb(211, 25, 25), 0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

.video-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.alert-continer {
    width: 300px;
    height: 80px;
    border-radius: 20px;
    font-size: calc(6px + 1vmin);
    margin: 10px auto;
}

.container-statistic {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
    background: rgb(20, 160, 90);
    background: linear-gradient(35deg, rgb(35, 212, 204) 0%, rgb(41, 139, 110) 35%, rgba(0, 212, 255, 1) 100%);
    box-shadow: -1px 10px 20px 0 rgba(12, 10, 11, 0.767), -2px 11px 22px 0 rgba(250, 248, 249, 0.137);
    margin: 10px auto;
    padding: 10px;
    width: 600px;
    border-radius: 30px;
}

@media screen and (max-width: 700px) {
    .container-statistic {
        width: 250px;
    }
}

