body {
  background-color: #225fb9;
  background-image: url('https://100idey.com.ua/image/cache/catalog/image/catalog/detailed/28/TL-65.webp');
  background-size: contain;
  -webkit-backdrop-filter: blur(4px);
  -moz-backdrop-filter: blur(4px);
  backdrop-filter: blur(4px);
}

.heart {
  position: relative;
  width: calc(40vmin);
  height: calc(60vmin);
  background-color: red;
  box-shadow: -1px 20px 40px #9b4141;
  border-radius: 25em 25em 0 0;
  transform: rotate(300deg);
}

.heart:before {
  position: absolute;
  width: calc(60vmin);
  height: calc(40vmin);
  left: 0;
  bottom: 0;
  content: "";
  background-color: red;
  box-shadow: 0px 3px 3px #444444;
  border-radius: 0 25em 25em 0;
}

.heart:hover:before,
.heart:hover {
  box-shadow: none;
  cursor: pointer;
}

.heart p {
  transform: rotate(60deg);
  position: absolute;
  width: 100%;
  left: calc((0vmin));
  bottom: calc((20vmin));
  text-align: center;
  font-size: calc(6px + 3vmin);
}

.heartWrapper {
  margin: 30px 5% 30px 5%;
}



.rotate {
  width: 300px;
}

.rotate p {
  display: block;
  overflow-wrap: break-word;
  margin: 2em 10% 2em 10%;
  padding: 10px;
  font-size: calc(6px + 3vmin);
  -webkit-user-select: none;
  user-select: none;
}

.grid {
  display: flex;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 20px;
  margin: 0 5% 2em 5%;
  justify-content: space-around;

}



@media screen and (orientation: landscape) and (max-width: 700px) {
  .grid {
    flex-direction: column;
  }

  .rotate {
    width: 200px;
  }
}

@media screen and (orientation: Portrait) and (max-width: 800px) {
  .grid {
    flex-direction: column;
  }
  .rotate {
    width: 200px;
  }
}


.heart {
  animation-name: anime;
  animation-duration: 300ms;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-timing-function: ease-in;
}

@keyframes anime {
  from {
    transform: scale(1) rotate(320deg);
  }
  to {
    transform: scale(1.2) rotate(315deg);
  }
}

