.dish-containers-corusel {
    position: relative;
    margin: 10px auto;
    display: flex;
    background-color: transparent;
    flex-direction: column;
    justify-content: space-between;
    align-content: space-around;
    flex-wrap: wrap;

}

.image-corusel {
    opacity: 1;
    display: block;
    transition: .5s ease;
    backface-visibility: hidden;
    width: 600px;
    height: 400px;
    border-radius: 20%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.6), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}


@media screen and (orientation: Portrait) {
    .image-corusel {
        width: 200px;
        height: 200px;
    }
}

@media screen and (max-width: 880px) {
    .image-corusel {
        width: 200px;
        height: 200px;
    }
}




.middle-corusel {
    display: block;
    transition: 1s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    font-size: calc(6px + 3vmin);
    -webkit-user-select: none;
    user-select: none;
    font-style: italic;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.dish-containers-corusel:hover .middle-corusel {
    opacity: 1;
}

.dish-containers-corusel:hover .image-corusel {
    opacity: 0.5;
}